/*-- Toggle Btn CSS Start --*/

.btn-toggle {
    text-align: center;
    width: 30px;
    position: fixed;
    z-index: 110;
    display: none;
    top: 115px;
    left: 12px;
    position: fixed;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
    cursor: pointer;
}
.btn-toggle.side {
    left: 8px;
}
.btn-toggle.team {
    right: 8px;
    left: auto;
    
}
.btn-toggle span {
    display: block;
    width: 30px;
    height: 3px;
    background: #6A707E;
    margin-bottom: 6px;
}
.btn-toggle.show span:nth-child(1) {
    -webkit-transform: rotate(-50deg) translate(-5px, 8px);
    transform: rotate(-50deg) translate(-5px, 8px);
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}
.btn-toggle.show span:nth-child(2) {
    opacity: 0;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}
.btn-toggle.show span:nth-child(3) {
    -webkit-transform: rotate(47deg) translate(-5px, -8px);
    transform: rotate(47deg) translate(-5px, -8px);
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}
/*-- Toggle Btn CSS End --*/

