/*--- Responsive Start ---*/
@media (max-width: 1700px) {
    .sidebar {
        max-width: 270px;
    }
    .spot-wrap {
        margin-left: 270px;
    }
    .search-spaot .form-control {
        width: 80%;
    }
    .search-spaot .btn-primary {
        width: 20%;
    }
}


@media (max-width: 1199px) {
    .sidebar {
        left: -330px;
        z-index: 100;
        -webkit-transition: 0.5s all ease;
        transition: 0.5s all ease;
        padding: 40px 0 30px;
    }
    .sidebar.show {
        left: 0;
    }
    .btn-toggle.side {
        display: block;
    }
    .spot-wrap {
        margin-left: 0;
    }
}


@media (max-width: 991px) {
    .navbar-light .navbar-toggler {
        display: none;
    }
    .navbar-light .navbar-collapse {
        display: block;
    }
    .spot-wrap {
        padding: 120px 15px 30px 15px;
    }
    .header {
        padding: 10px 0;
    }
    .logo a {
        font-size: 30px;
    }
    .btn-toggle {
        top: 75px;
    }
    .search-spaot .btn-primary {
        font-size: 20px;
        height: 45px;
    }
    .search-spaot .form-control {
        
        height: 45px;
    }
    .sidebar {
        top: 60px;
    }
}

@media (max-width: 767px) {
    .search-spaot .form-control {
        width: 70%;
    }
    .search-spaot .btn-primary {
        width: 30%;
    }
}

@media (max-width: 640px) {


}

@media (max-width: 480px){
    .search-spaot {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
    .search-spaot .form-control {
        padding: 0 15px;
